<script>
import { myManageinfo } from '@/http/service/mine'
import personalApi from '@/http/service/index';
// import util from '@/utils/utils';

import Vue from 'vue';
import { ImagePreview } from 'vant';
Vue.use(ImagePreview);
export default {
  mounted() {
    this.myManageinfo()
    if (this.$route.query.repairSn) {
      this.getInfor(this.$util.strJSONParse(this.$route.query.repairSn, {}).repairSn)
      this.orderid = this.$util.strJSONParse(this.$route.query.repairSn, {}).orderProductId
    }
    this.storeCode = JSON.parse(this.$route.query.repairSn).storeCode
  },
  data() {
    return {
      userInfo: {},
      commentImgs: [], // 图片预览
      ImagePreview: '', // 图片预览
      picUrl: this.$cdnImageUrl + '/pc/shopPic.png', // 默认商家图片
      typeActual: '', // 退款类型转化
      orderOne: {},
      order: [],
      orderid: null, // 订单id
      storeCode: '', // 商家88码
    }
  },
  methods: {
    myManageinfo() {
      myManageinfo().then(({ data }) => {
        this.userInfo = data.data
      })
    },
    /**
   * @description  协商历史页面接口
   */
    getInfor(val) {
      const data = {
        repairSn: val
        // repairSn: 'WD192320786992TH2' // 逆向单号 测试(退货单生成：WD192320786787TH2用户撤销：WD192320786935TH2 商家同意退换货：WD192319785985TH2 WD192320786948TH2 用户填写快递信息:WD192320786953TH2 商家驳回：WD192320786969TH2 商家同意退款:WD192320786981TH2 商家驳回退款：WD192320786992TH2)
      }
      personalApi.getOrderRepairHistory(data).then(res => {
        // 获取长度
        const long = res.data.data.length - 1

        if (long === 0) {
          this.orderOne = this.$util.strJSONParse(JSON.stringify(res.data.data), [])[long]
        } else {
          this.orderOne = this.$util.strJSONParse(JSON.stringify(res.data.data), [])[long]
          this.order = this.$util.strJSONParse(JSON.stringify(res.data.data), [])
          this.order.splice(long, 1)
        }
        // 退款类型转化
        if (String(res.data.data[long].description.typeActual) === '1') {
          this.typeActual = '退货退款'
        } else if (String(res.data.data[long].description.typeActual) === '2') {
          this.typeActual = '退货不退款'
        } else if (String(res.data.data[long].description.typeActual) === '3') {
          this.typeActual = '仅退款'
        }
      })
    },
    /**
   * @description  时间戳转年月日时分
   */
    timestampToTime(timestamp) {
      const date = new Date(timestamp * 1000);// 时间戳为10位需*1000，时间戳为13位的话不需乘1000
      const Y = date.getFullYear() + '年';
      const M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '月';
      const D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + '日 ';
      const h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
      const m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
      var s = (date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds());
      return Y + M + D + h + m + s;
    },
    /**
   * @description  小二审核类型转化
   */
    auditType(val) {
      let type = ''
      if (val === 'TH') {
        type = '退货审核'
      } else {
        type = '退款审核'
      }
      return type
    },
    /**
   * @description  联系客服
   */
    contact() {
      // 如果是海尔app隐藏客服头部栏，使用app内头部栏，否则使用客服工具头部栏
      this.$store.dispatch('goPageByType', {
        type: 29,
        params: {
          accountId: this.storeCode || '',
          sceneId: 'cj097'
        }
      })
      // }
    },
    /**
     * @description 验证传入的88码是否开通商家客服
    */
    isCanGetCustomer(storeCode) {
      return new Promise((resolve, reject) => {
        const data = {
          storeCode: storeCode
        };
        personalApi.getCustomer(data).then(res => {
          if (res.data.success) {
            resolve(res.data.result)
          }
        })
      })
    },
    /**
     *查看图片
     */
    lookStart(i, arr) {
      let index = arr.indexOf(i)
      this.ImagePreview = ImagePreview(
        {
          images: arr,
          startPosition: index,
        }
      );
    }
  }
}
</script>
